"use client"

import { useState, useEffect } from "react"
import { Plus, Trash2, Edit, Film, Calendar, MonitorPlay } from 'lucide-react'

interface Movie {
    id: number
    title: string
    description: string
    genre: string
    language: string
    duration: number
    release_date: string
    poster_url: string
    backdrop_url: string
    trailer_url?: string
    rating: number
}

export default function AdminMoviesPage() {
    const [movies, setMovies] = useState<Movie[]>([])
    const [isLoading, setIsLoading] = useState(true)
    const [editingId, setEditingId] = useState<number | null>(null)
    const [formData, setFormData] = useState({
        title: "",
        description: "",
        genre: "",
        language: "",
        duration: 0,
        release_date: "",
        poster_url: "",
        backdrop_url: "",
        trailer_url: "",
        rating: 0
    })

    const getApiUrl = (endpoint: string) => {
        const baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'
        return baseUrl.endsWith('/api') ? `${baseUrl}/${endpoint}` : `${baseUrl}/api/${endpoint}`
    }

    useEffect(() => {
        fetchMovies()
    }, [])

    const fetchMovies = async () => {
        try {
            // Logic adjusted to handle if env var already has /api
            const baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'
            const url = baseUrl.endsWith('/api') ? `${baseUrl}/movies` : `${baseUrl}/api/movies`

            const res = await fetch(url)
            const data = await res.json()
            if (Array.isArray(data)) {
                setMovies(data)
            } else {
                console.error("API returned non-array:", data)
                setMovies([])
            }
        } catch (error) {
            console.error("Failed to fetch movies", error)
            setMovies([])
        } finally {
            setIsLoading(false)
        }
    }

    const resetForm = () => {
        setEditingId(null)
        setFormData({
            title: '',
            description: '',
            genre: '',
            language: '',
            duration: 0,
            release_date: '',
            poster_url: '',
            backdrop_url: '',
            trailer_url: '',
            rating: 0
        })
    }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()
        const payload = {
            ...formData,
            // Ensure genre is sent as an array of strings
            genre: typeof formData.genre === 'string'
                ? formData.genre.split(',').map((g: string) => g.trim())
                : formData.genre
        }

        try {
            const url = editingId
                ? `${getApiUrl('movies')}/${editingId}`
                : getApiUrl('movies')

            const method = editingId ? 'PUT' : 'POST'

            const res = await fetch(url, {
                method: method,
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload)
            })
            if (res.ok) {
                fetchMovies()
                resetForm()
                alert(editingId ? 'Movie updated successfully' : 'Movie added successfully')
            } else {
                alert('Failed to save movie')
            }
        } catch (error) {
            console.error(error)
        }
    }

    const handleEdit = (movie: Movie) => {
        setEditingId(movie.id)
        setFormData({
            title: movie.title,
            description: movie.description || '',
            genre: Array.isArray(movie.genre) ? movie.genre.join(', ') : movie.genre,
            language: movie.language || '',
            duration: movie.duration || 0,
            release_date: movie.release_date || '',
            poster_url: movie.poster_url || '',
            backdrop_url: movie.backdrop_url || '',
            trailer_url: movie.trailer_url || '',
            rating: movie.rating || 0
        })
        window.scrollTo({ top: 0, behavior: 'smooth' })
    }

    const handleDelete = async (id: number) => {
        if (!confirm('Are you sure?')) return
        try {
            await fetch(`${getApiUrl('movies')}/${id}`, { method: 'DELETE' })
            fetchMovies()
        } catch (error) {
            console.error(error)
        }
    }

    const fetchFromTMDB = async () => {
        if (!formData.title) {
            alert("Please enter a movie title first")
            return
        }

        setIsLoading(true)
        try {
            const url = getApiUrl(`tmdb/search-movie?title=${encodeURIComponent(formData.title)}`)

            const res = await fetch(url)
            const data = await res.json()

            if (res.ok) {
                setFormData({
                    ...formData,
                    title: data.title,
                    description: data.description || "",
                    genre: Array.isArray(data.genre) ? data.genre.join(', ') : (data.genre || ""),
                    language: data.language || "English",
                    duration: data.duration || 0,
                    release_date: data.release_date || "",
                    poster_url: data.poster_url || "",
                    backdrop_url: data.backdrop_url || "",
                    trailer_url: data.trailer_url || "",
                    rating: data.rating || 0
                })
                alert("Movie details fetched from TMDB!")
            } else {
                alert(data.error || "Failed to fetch from TMDB")
            }
        } catch (error) {
            console.error("TMDB Fetch Error", error)
            alert("Error fetching data")
        } finally {
            setIsLoading(false)
        }
    }

    return (
        <div className="space-y-8">
            <h1 className="text-2xl font-bold text-foreground">Manage Movies</h1>

            {/* Add/Edit Movie Form */}
            <div className="bg-card p-6 rounded-lg shadow-md border border-border">
                <div className="flex justify-between items-center mb-4">
                    <h2 className="text-lg font-semibold flex items-center text-foreground">
                        {editingId ? <Edit className="mr-2" size={20} /> : <Plus className="mr-2" size={20} />}
                        {editingId ? 'Edit Movie' : 'Add New Movie'}
                    </h2>
                    {editingId && (
                        <button onClick={resetForm} className="text-sm text-muted-foreground hover:text-foreground">
                            Cancel Edit
                        </button>
                    )}
                </div>

                <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="flex gap-2">
                        <input type="text" placeholder="Title" required className="flex-1 border border-input bg-secondary text-foreground p-2 rounded focus:ring-2 focus:ring-ring focus:outline-none"
                            value={formData.title} onChange={e => setFormData({ ...formData, title: e.target.value })} />
                        <button type="button" onClick={fetchFromTMDB} className="bg-accent text-accent-foreground px-3 py-2 rounded text-sm hover:bg-accent/90 transition-colors whitespace-nowrap">
                            Fetch TMDB
                        </button>
                    </div>
                    <input type="text" placeholder="Genre (comma separated)" required className="border border-input bg-secondary text-foreground p-2 rounded focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.genre} onChange={e => setFormData({ ...formData, genre: e.target.value })} />
                    <input type="text" placeholder="Language" required className="border border-input bg-secondary text-foreground p-2 rounded focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.language} onChange={e => setFormData({ ...formData, language: e.target.value })} />
                    <input type="number" placeholder="Duration (min)" required className="border border-input bg-secondary text-foreground p-2 rounded focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.duration} onChange={e => setFormData({ ...formData, duration: Number(e.target.value) })} />
                    <input type="date" required className="border border-input bg-secondary text-foreground p-2 rounded focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.release_date} onChange={e => setFormData({ ...formData, release_date: e.target.value })} />
                    <input type="number" placeholder="Rating (0-10)" required className="border border-input bg-secondary text-foreground p-2 rounded focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.rating} onChange={e => setFormData({ ...formData, rating: Number(e.target.value) })} />
                    <input type="text" placeholder="Poster URL" required className="border border-input bg-secondary text-foreground p-2 rounded md:col-span-2 focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.poster_url} onChange={e => setFormData({ ...formData, poster_url: e.target.value })} />
                    <input type="text" placeholder="Backdrop URL" required className="border border-input bg-secondary text-foreground p-2 rounded md:col-span-2 focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.backdrop_url} onChange={e => setFormData({ ...formData, backdrop_url: e.target.value })} />
                    <input type="text" placeholder="Trailer URL (YouTube)" className="border border-input bg-secondary text-foreground p-2 rounded md:col-span-2 focus:ring-2 focus:ring-ring focus:outline-none"
                        value={formData.trailer_url} onChange={e => setFormData({ ...formData, trailer_url: e.target.value })} />
                    <textarea placeholder="Description" required className="border border-input bg-secondary text-foreground p-2 rounded md:col-span-2 focus:ring-2 focus:ring-ring focus:outline-none" rows={3}
                        value={formData.description} onChange={e => setFormData({ ...formData, description: e.target.value })} />

                    <button type="submit" className={`p-2 rounded md:col-span-2 transition-colors ${editingId ? 'bg-primary text-primary-foreground hover:bg-primary/90' : 'bg-green-600 text-white hover:bg-green-700'}`}>
                        {editingId ? 'Update Movie' : 'Add Movie'}
                    </button>
                </form>
            </div>

            {/* Movie List */}
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
                {isLoading ? <p className="text-muted-foreground">Loading...</p> : movies.map(movie => (
                    <div key={movie.id} className="bg-card rounded-lg shadow overflow-hidden relative group border border-border">
                        <img src={movie.poster_url} alt={movie.title} className="w-full h-64 object-cover" />
                        <div className="p-4">
                            <h3 className="font-bold truncate text-foreground">{movie.title}</h3>
                            <p className="text-sm text-muted-foreground">{Array.isArray(movie.genre) ? movie.genre.join(', ') : movie.genre}</p>
                        </div>
                        <div className="absolute top-2 right-2 flex gap-2 opacity-0 group-hover:opacity-100 transition-opacity">
                            <button onClick={() => handleEdit(movie)}
                                className="bg-primary text-primary-foreground p-2 rounded-full hover:bg-primary/90">
                                <Edit size={16} />
                            </button>
                            <button onClick={() => handleDelete(movie.id)}
                                className="bg-destructive text-destructive-foreground p-2 rounded-full hover:bg-destructive/90">
                                <Trash2 size={16} />
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}
